import React from "react";
import QuickConfigButton from "./QuickConfigButton";

export default function QuickConfiguration() {
  const options = [
    {
      title: "Previso Recorrente",
      description: "Configure previses que se repetem automaticamente",
      icon: "repeat",
      primary: true,
    },
    {
      title: "Alertas de Desvio",
      description: "Receba notificaes quando h desvios significativos",
      icon: "alert",
    },
    {
      title: "Relatrio Automtico",
      description: "Gere e distribua relatrios automaticamente",
      icon: "report",
    },
    {
      title: "Sincronizao de Dados",
      description: "Sincronize dados automaticamente com sistemas externos",
      icon: "sync",
    },
  ];

  return (
    <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
      <h2 className="text-xl font-semibold text-gray-900 mb-6">
        Configurao Rpida
      </h2>

      <div className="grid grid-cols-2 gap-4">
        {options.map((option, index) => (
          <QuickConfigButton key={index} {...option} />
        ))}
      </div>
    </div>
  );
}
