import { useState, useEffect } from 'react';
import api from '../utils/Api';

export function useUsers() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Carrega usurios ao montar o componente
  useEffect(() => {
    fetchUsers();
    // eslint-disable-next-line
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  /**
   * Busca todos os usurios da API
   */
const fetchUsers = async (includeInactive = false) => {
    try {
      setLoading(true);
      setError(null);

      const response = await api.get('/users/', {
        params: { include_inactive: includeInactive }
      });

      console.log(' Usurios carregados:', response.data);
      
      // Normaliza todos os usurios para garantir que 'tipo' existe
      const normalizedUsers = (response.data.usuarios || []).map(user => ({
        ...user,
        tipo: user.tipo || user.role
      }));
      
      setUsers(normalizedUsers);
      
    } catch (err) {
      console.error(' Erro ao carregar usurios:', err);
      setError(err.response?.data?.detail || 'Erro ao carregar usurios');
      setUsers([]);
    } finally {
      setLoading(false);
    }
  };

  /**
   * Cria novo usurio
   */
const addUser = async (userData) => {
    try {
      setLoading(true);

      // Payload para API
      const payload = {
        nome: userData.nome,
        email: userData.email,
        role: userData.tipo, // Frontend usa 'tipo', API usa 'role'
        senha: userData.senha,
        ativo: userData.ativo ?? true
      };

      console.log(' Criando usurio:', payload);

      const response = await api.post('/users/', payload);

      console.log(' Usurio criado:', response.data);

      // Normaliza resposta para garantir que 'tipo' existe
      const normalizedUser = {
        ...response.data,
        tipo: response.data.tipo || response.data.role
      };

      // Atualiza lista local
      setUsers(prev => [normalizedUser, ...prev]);

      return { 
        success: true, 
        user: normalizedUser
      };

    } catch (err) {
      console.error(' Erro ao criar usurio:', err);
      const errorMessage = err.response?.data?.detail || 'Erro ao criar usurio';
      
      return { 
        success: false, 
        error: errorMessage 
      };
    } finally {
      setLoading(false);
    }
  };

  /**
   * Atualiza usurio existente
   */
const updateUser = async (userId, userData) => {
    try {
      setLoading(true);

      // Payload para API (apenas campos que mudaram)
      const payload = {};
      
      if (userData.nome !== undefined) payload.nome = userData.nome;
      if (userData.email !== undefined) payload.email = userData.email;
      if (userData.tipo !== undefined) payload.role = userData.tipo;
      if (userData.ativo !== undefined) payload.ativo = userData.ativo;

      console.log(' Atualizando usurio:', userId, payload);

      const response = await api.put(`/users/${userId}`, payload);

      console.log(' Usurio atualizado:', response.data);

      // Normaliza resposta para garantir que 'tipo' existe
      const normalizedUser = {
        ...response.data,
        tipo: response.data.tipo || response.data.role
      };

      // Atualiza lista local
      setUsers(prev => prev.map(user => 
        user.id_usuario === userId ? normalizedUser : user
      ));

      return { 
        success: true, 
        user: normalizedUser
      };

    } catch (err) {
      console.error(' Erro ao atualizar usurio:', err);
      const errorMessage = err.response?.data?.detail || 'Erro ao atualizar usurio';
      
      return { 
        success: false, 
        error: errorMessage 
      };
    } finally {
      setLoading(false);
    }
  };

  /**
   * Desativa usurio (soft delete)
   */
  const deleteUser = async (userId) => {
    try {
      setLoading(true);

      console.log(' Desativando usurio:', userId);

      const response = await api.delete(`/users/${userId}`);

      console.log(' Usurio desativado:', response.data);

      // Atualiza lista local - marca como inativo
      setUsers(prev => prev.map(user => 
        user.id_usuario === userId 
          ? { ...user, ativo: false } 
          : user
      ));

      return { 
        success: true, 
        message: response.data.message 
      };

    } catch (err) {
      console.error(' Erro ao desativar usurio:', err);
      const errorMessage = err.response?.data?.detail || 'Erro ao desativar usurio';
      
      return { 
        success: false, 
        error: errorMessage 
      };
    } finally {
      setLoading(false);
    }
  };

  /**
   * Reativa usurio
   */
  const reactivateUser = async (userId) => {
    try {
      setLoading(true);

      console.log(' Reativando usurio:', userId);

      const response = await api.put(`/users/${userId}`, { ativo: true });

      console.log(' Usurio reativado:', response.data);

      // Atualiza lista local
      setUsers(prev => prev.map(user => 
        user.id_usuario === userId ? response.data : user
      ));

      return { 
        success: true, 
        user: response.data 
      };

    } catch (err) {
      console.error(' Erro ao reativar usurio:', err);
      const errorMessage = err.response?.data?.detail || 'Erro ao reativar usurio';
      
      return { 
        success: false, 
        error: errorMessage 
      };
    } finally {
      setLoading(false);
    }
  };

  return {
    users,
    loading,
    error,
    addUser,
    updateUser,
    deleteUser,
    reactivateUser,
    refreshUsers: fetchUsers
  };
}